<template>
  <div v-loading="loading.all">
		<TremendousCard class="mb-6" />
    <div class="flex flex-wrap items-center justify-between gap-6 mb-6">
      <Input placeholder="Search" v-model="data.search" block icon class="w-full md:max-w-2xl">
        <template #icon>
          <span class="material-icons">search</span>
        </template>
      </Input>
      <div class="flex items-center gap-4">
        <ExpeditedPayout
        :expeditedConfig="state.expedited_payouts"
        :loading="loading.updateExpedited"
        @update="handleUpdateExpedited"
        />
        <CustomPayout
        label="Custom Payout"
        :disabled="isAvailableCustom"
        @create="handleCustomPayout"
        />
      </div>
    </div>
    <Table :data="payoutMethods">
      <TableColumn prop="is_enable" label="">
        <template #default="scope">
          <Checkbox
            v-model="scope.row.is_enable"
            :value="1"
            :value-off="0"
            class="py-2"
            type="toggle"
            @input="updatePaymentMethodSwitch(scope.row)"
          />
        </template>
      </TableColumn>
      <TableColumn prop="name" label="Payout Method" width="240">
        <template #default="scope">
          <div class="flex flex-wrap items-center space-x-4">
            <img
              :src="scope.row.icon"
              class="w-8 my-auto"
              :alt="scope.row.name"
            />
            <div class="my-auto whitespace-nowrap">
              {{ scope.row.name }}
            </div>
            <div
              v-if="scope.row.is_custom"
              class="bg-teal-base text-white whitespace-nowrap flex items-center justify-center rounded-full px-4 h-8 text-sm">
              Custom
            </div>
            <div v-if="loading.update.get(scope.row.id)" class="relative p-4">
              <transition name="fade">
                <Loading />
              </transition>
            </div>
          </div>
        </template>
      </TableColumn>
      <TableColumn prop="total_location" label="Locations" width="200">
        <template #default="scope">
          <div
            class="text-teal flex space-x-4 text-teal-04"
            @click="showSelectLocation(scope.row)"
          >
            <div class="my-auto font-semibold text-teal-700">
              {{ scope.row.total_location }} location(s) selected
            </div>
            <div class="my-auto">
              <i class="material-icons">settings</i>
            </div>
          </div>
        </template>
      </TableColumn>
      <TableColumn prop="min_amount" label="Min Amount" width="150">
        <template #default="scope">
          <Input
            v-model="scope.row.min_amount"
            type="number"
            placeholder="Amount"
            min="0"
            @keypress="filterNumber"
            @focus-out="updatePaymentMethod(scope.row)"
            icon
            icon-block
            dense
          >
            <template #icon>
              <span class="text-lg">{{ me.currency_symbol }}</span>
            </template>
          </Input>
        </template>
      </TableColumn>
      <TableColumn prop="max_amount" label="Max Amount" width="150">
        <template #default="scope">
          <Input
            v-model="scope.row.max_amount"
            type="number"
            placeholder="Amount"
            min="0"
            @keypress="filterNumber"
            @focus-out="updatePaymentMethod(scope.row)"
            icon
            icon-block
            dense
          >
            <template #icon>
              <span class="text-lg">{{ me.currency_symbol }}</span>
            </template>
          </Input>
        </template>
      </TableColumn>
      <TableColumn label="Set Bonus Percentage" width="150">
        <template #default="{ row }">
          <Input
            v-model="row.price_increase"
            v-maska="'#*.##'"
            placeholder="Amount"
            @focus-out="updatePaymentMethod(row)"
            icon
            right
            icon-block
            dense
          >
            <template #icon>
              <span class="text-lg">%</span>
            </template>
          </Input>
        </template>
      </TableColumn>
      <TableColumn prop="desc" label="Description" width="500">
        <template #default="scope">
          <Input
            v-model="scope.row.desc"
            type="textarea"
            placeholder="Your customers will see this message when they choose this payout method."
            @focus-out="updatePaymentMethod(scope.row)"
            class="w-full"
          />
        </template>
      </TableColumn>
      <TableColumn prop="action">
        <template #default="scope">
          <div
            v-if="scope.row.is_custom"
            class="lg:opacity-0 group-hover:opacity-100 transition-opacity flex gap-x-2">
            <CustomPayout edit :data="scope.row" @update="handleCustomPayout" />
            <CustomPayout trash :data="scope.row" @delete="handleCustomPayout" />
          </div>
          <!-- <template v-if="scope.row.name === 'Paper Check'">
            <Button
              view="smooth"
              color="teal"
              :to="{ name: 'settingCheckSettings' }"
              link
              dense
              >Check Settings</Button
            >
          </template> -->
          <template
            v-if="scope.row.name === 'Bank Transfer' && scope.row.id === 4"
          >
            <Button
              color="teal"
              @click="openModalBankTransferSetting"
              ><i class="material-icons">settings</i> Settings</Button
            >
          </template>
        </template>
      </TableColumn>
    </Table>
    <div class="mt-6">
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="state.data" />
    </div>
  </div>

  <Modal
    v-model="modal.show"
    title="Select Locations"
    v-loading="loading.locations"
    @close="modal.show = false"
    size="xl"
    id="settingPayoutMethodsLocations"
    data-intercom-target="Setting Payout Methods Locations"
  >
    <SelectLocation
      :data="selectPaymentMethod"
      @closeSelectLocation="closeSelectLocation"
    />
    <template #footer />
  </Modal>

  <Modal
    v-model="modalBankTransferSetting"
    title="Bank Transfer Settings"
    size="sm"
    @close="unsetDataModalBankTransferSetting"
  >
    <template #default>
      <div class="flex bg-blue-lighten px-4 py-4 rounded-lg text-blue-04">
        <div>
          <i class="material-icons">info</i>
        </div>
        <div class="flex-grow pt-1 px-4">
          Customize the input fields required for customer that request Bank
          Transfer. <i>(Minimum of 1, Maximum of 3)</i>
        </div>
      </div>
      <div v-loading="loadingBankTransferSetting.show" class="mt-6">
        <template v-if="Array.isArray(dataBankTransferSetting.bank_transfer)">
          <Draggable  :list="dataBankTransferSetting.bank_transfer" @start="handleDragging" @end="handlePlaced" handle=".drag-handle">
            <div
              v-for="(
                bankTransferField, key
              ) in dataBankTransferSetting.bank_transfer"
              class="grid grid-cols-10 my-4"
              :key="bankTransferField.attribute_name"
            >
              <div class="flex items-center justify-center">
                <i class="material-icons mt-8 px-3 select-none drag-handle" style="cursor: grab">drag_handle</i>
              </div>
              <div class="col-span-3 mr-4 flex items-end">
                <Select v-model="bankTransferField.is_required" name="label" by="value" placeholder="Requirement" :items="requirementOptions" class="w-full" />
              </div>
              <div class="flex items-center col-span-6">
                <Input
                  v-model="bankTransferField.name"
                  label="Field Label"
                  block
                  :placeholder="bankTransferFieldPlaceholder(key)"
                  class="mr-4"
                />
                <Transition name="fade">
                  <Button
                    v-if="key !== 0 && !isBankTransferFieldSorting"
                    @click="handleBankTransferRemoveField(key)"
                    color="red"
                    class="mt-8"
                    :style="{paddingLeft: '.5em', paddingRight: '.5em'}"
                  >
                    <i class="material-icons px-2">delete</i>
                  </Button>
                </Transition>
              </div>
            </div>
          </Draggable>
        </template>
        <Button
          :disabled="loadingBankTransferSetting.update"
          @click="handleBankTransferAddField"
          v-if="isShowAddBankTransferField"
          color="teal"
          dense
          >Add Field</Button
        >
      </div>
    </template>
    <template #footer>
      <div class="flex items-center justify-between">
        <a
          class="px-4 py-3 text-red-04 hover:text-red-03"
          href="#!"
          @click.prevent="closeModalBankTransferSetting"
          >Close</a
        >
        <Button
          :disabled="isDisableUpdateBankTransferField"
          @click="handleBankTransferUpdate"
          color="teal"
          :loading="loadingBankTransferSetting.update"
          >Save</Button
        >
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, ref, computed, watch } from 'vue'
import { useUser } from 'composable/user'
import { usePaymentMethod } from 'composable/setting'
import { useBankTransferSetting } from 'composable/setting/payment'

import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Loading from 'bbUi/components/loading/loading.vue'
import Select from 'bbUi/components/select/select.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Button from 'bbUi/components/button/button.vue'
import CustomPayout from 'components/CustomPayout/index.vue'
import ExpeditedPayout from 'components/ExpeditedPayout/index.vue'
import TremendousCardPayout from "components/Shared/TremendousCard/TremendousCardPayout.vue";
import { VueDraggableNext } from 'vue-draggable-next'

import { filterNumber } from 'utils/number'
import SelectLocation from 'components/PaymentMethod/SelectLocation.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { ElMessage } from 'element-plus'
import debounce from 'utils/debounce'

export default defineComponent({
  components: {
    Card,
    Checkbox,
    Input,
    Loading,
    Select,
    Pagination,
    Breadcrumb,
    Button,
    SelectLocation,
    CustomPayout,
		TremendousCard: TremendousCardPayout,
    Draggable: VueDraggableNext,
    Modal,
    ExpeditedPayout
  },
  async setup(props, { emit }) {
    const { me } = useUser()
    const {
      data,
      state,
      loading,
      modal,
      actionAll,
      actionUpdate,
      actionUpdateSwitch,
      actionUpdateExpedited
    } = usePaymentMethod()
    const {
      data: dataBankTransferSetting,
      loading: loadingBankTransferSetting,
      modal: modalBankTransferSetting,
      openModal: openModalBankTransferSetting,
      closeModal: closeModalBankTransferSetting,
      unsetData: unsetDataModalBankTransferSetting,
      actionUpdate: actionUpdateBankTransferSetting
    } = useBankTransferSetting()
    const selectPaymentMethod = ref(null)
    const requirementOptions = ref([
      { value: true, label: 'Required' },
      { value: false, label: 'Not Required' }
    ])
    const isBankTransferFieldSorting = ref(false)

    const isShowAddBankTransferField = computed(
      () => dataBankTransferSetting.bank_transfer.length < 3
    )
    const isDisableUpdateBankTransferField = computed(
      () =>
        Array.isArray(dataBankTransferSetting.bank_transfer) ? dataBankTransferSetting.bank_transfer.find(
          (field) => field.name === ''
        ) !== undefined : true
    )
    const payoutMethods = computed(() => {
      const filteredPayouts = state.value.data.data.filter((payoutMethod) => {
        // remove currently disabled paper and express check payout method from the payout method list
        const excludedPayoutId = [17] // paper check(2) & express check ID
        const isExcludedPayout =
          excludedPayoutId.includes(payoutMethod.id) &&
          payoutMethod.is_enable === 0
        return !isExcludedPayout
      })
      return filteredPayouts
    })
    const isAvailableCustom = computed(() =>
      state.value.add_payout_allowed ? false : true
    )

    const updatePaymentMethod = (item) => {
      actionUpdate({
        id: item.id,
        data: {
          is_enable: item.is_enable,
          min_amount: item.min_amount,
          max_amount: item.max_amount,
          price_increase: item.price_increase
            ? typeof item.price_increase === 'string'
              ? parseFloat(item.price_increase)
              : item.price_increase
            : 0,
          desc: item.desc
        }
      })
    }
    const updatePaymentMethodSwitch = async (item) => {
      const payload = {
        payment_id: item.id,
        is_enable: item.is_enable
      }

      actionUpdateSwitch(payload).finally((res) => actionAll())
    }

    const showSelectLocation = (data) => {
      selectPaymentMethod.value = data
      modal.show = true
    }
    const closeSelectLocation = async () => {
      selectPaymentMethod.value = null
      modal.show = false
      actionAll()
    }
    const handleBankTransferRemoveField = (key) => {
      dataBankTransferSetting.bank_transfer.splice(key, 1)
    }
    const handleBankTransferAddField = () => {
      dataBankTransferSetting.bank_transfer.push({
        name: '',
        attribute_name: '',
        is_required: true
      })
    }
    const handleBankTransferUpdate = async () => {
      await actionUpdateBankTransferSetting((isErr) => {
        if (!isErr) {
          modalBankTransferSetting.value = false
          ElMessage({
            type: 'success',
            message: 'Bank transfer setting updated'
          })
        }
      })
    }
    const bankTransferFieldPlaceholder = (key) => {
      switch (key) {
        case 0:
          return 'Account Name'
        case 1:
          return 'Account Number'
        case 2:
          return 'Routing Number'
        default:
          return ''
      }
    }
    const handleRequestPayoutMethod = () =>
      window.open(
        'https://www.reusely.com/requestpayment',
        '_blank',
        'noreferrer noopener'
      )
    const handleCustomPayout = async () => {
      await actionAll()
    }
    const handleDragging = () => {
      isBankTransferFieldSorting.value = true
    }
    const handlePlaced = () => {
      isBankTransferFieldSorting.value = false
    }

    const handleUpdateExpedited = async (callback) => {
      await actionUpdateExpedited(callback)
    }

    watch(() => ({...data}), debounce(async () => {
      await actionAll()
    }, 400), {
      deep: true
    })

    watch(() => ({limit: data.limit, search: data.search}), () => data.page = 1, {
      deep: true
    })

    await actionAll()

    return {
      me,
      data,
      dataBankTransferSetting,
      state,
      loading,
      loadingBankTransferSetting,
      updatePaymentMethod,
      updatePaymentMethodSwitch,
      filterNumber,
      modal,
      modalBankTransferSetting,
      selectPaymentMethod,
      requirementOptions,
      isShowAddBankTransferField,
      isDisableUpdateBankTransferField,
      showSelectLocation,
      closeSelectLocation,
      openModalBankTransferSetting,
      closeModalBankTransferSetting,
      unsetDataModalBankTransferSetting,
      handleBankTransferRemoveField,
      handleBankTransferAddField,
      handleBankTransferUpdate,
      bankTransferFieldPlaceholder,
      payoutMethods,
      isAvailableCustom,
      isBankTransferFieldSorting,
      handleRequestPayoutMethod,
      handleCustomPayout,
      handleDragging,
      handlePlaced,
      handleUpdateExpedited,
    }
  }
})
</script>
