<template>
    <div>
      <Button view="outlined" color="teal" @click="modalExpedited = true">
        <ExpeditedIcon class="mr-1 inline-flex" /> Expedited Payouts
      </Button>
  
      <Modal
        v-model="modalExpedited"
        title="Expedited Payouts"
        size="sm"
      >
        <template #default>
          <p>An option for customers seeking faster processing for their offers. The amount you set will be deducted from their offer automatically and will be visible to them.</p>
          <div class="flex gap-3 p-4 bg-orange-lighten mt-4 rounded-lg">
            <span class="material-icons text-orange-03">info</span>
            <p class="text-orange-03"><span class="font-semibold">Important tip:</span> We advise using this option carefully, as charging users an expedited service fee while still delaying their processing could lead to a poor experience. <br/><br/>
            The Expedited Payouts option will not be offered to your customers if the Expedited Payout Service Fee is higher than the total payout.
            </p>
          </div>
          <div class="rounded-lg border border-grey-01 p-4 mt-4">
            <Checkbox
              :value="true"
              :value-off="false"
              type="toggle"
              v-model="expeditedState.is_enable_expedited_payout"
              >
              Turn on Expedited Payouts
            </Checkbox>
            <template v-if="expeditedState.is_enable_expedited_payout">
              <Input
                v-model="expeditedState.expedited_payout_fee"
                v-maska="'#*.##'"
                label="Expedited Payout Service Fee"
                icon
                icon-block
                block
                class="mt-4"
                :view="validation.expedited_payout_fee.$invalid && 'danger' || 'default'"
              >
                <template #icon>
                  <Currency only-symbol class="text-xl" />
                </template>
              </Input>
              <small class="text-red-base block my-2" v-for="(error, errorIndex) of validation.expedited_payout_fee.$errors" :key="errorIndex">Expedited Payout Service Fee is required. </small>
              <label class="mt-6" :class="validation.expedited_payout_desc.$invalid && 'text-red-base' || ''">
                Expedited Payouts Description
              </label>
              <textarea v-model="expeditedState.expedited_payout_desc" rows="4" class="w-full border rounded-lg p-4" :class="validation.expedited_payout_desc.$invalid && 'border-red-04' || 'border-grey-01'" />
              <small class="text-red-base block my-2" v-for="(error, errorIndex) of validation.expedited_payout_desc.$errors" :key="errorIndex">Expedited Payouts Description is required. </small>
            </template>
          </div>
        </template> 
        <template #footer>
          <div class="flex items-center justify-between">
            <Button @click="modalExpedited = false" :disabled="loading" color="teal" view="smooth">Cancel</Button>  
            <Button :loading="loading" @click="handleUpdateExpedited" color="teal">Save Changes</Button>  
          </div>
        </template> 
      </Modal>
    </div>
  </template>
  
  <script>
  import { defineComponent, ref, computed } from 'vue'
  import Button from 'bbUi/components/button/button.vue'
  import Modal from 'bbUi/components/modal/modal.vue'
  import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
  import Input from 'bbUi/components/input/input.vue'
  import Currency from 'components/Currency.vue'
  import { ElMessage } from 'element-plus'
  import ExpeditedIcon from './ExpeditedIcon.vue'
  import { requiredIf } from '@vuelidate/validators'
  import useVuelidate from '@vuelidate/core'
  
  export default defineComponent({
    name: 'ExpeditedPayout',
    components: {
      Button,
      Modal,
      Checkbox,
      Input,
      Currency,
      ExpeditedIcon
    },
    props: {
      expeditedConfig: {
        type: Object,
        required: true
      },
      loading: {
        type: Boolean,
        default: false
      }
    },
    emits: ['update'],
    setup(props, { emit }) {
      const modalExpedited = ref(false)
      
      const expeditedState = computed(() => {
        return props.expeditedConfig
      })

      const rules = {
        expedited_payout_fee: {
          required: requiredIf(() => expeditedState.value.is_enable_expedited_payout),
        },
        expedited_payout_desc: {
          required: requiredIf(() => expeditedState.value.is_enable_expedited_payout),
        }
      }

      const validation = useVuelidate(rules, expeditedState, {
          $lazy: true
      })

      const validate = () => {
        validation.value.$validate()
        return !validation.value.$invalid
      }
  
      const handleUpdateExpedited = async () => {
        if (!validate()) return
        emit('update', () => {
          modalExpedited.value = false
          ElMessage({
            type: 'success',
            message: 'Expedited Payouts updated'
          })
        })
      }
  
      return {
        modalExpedited,
        expeditedState,
        handleUpdateExpedited,
        rules,
        validation,
        validate
      }
    }
  })
  </script>